import { Button, makeStyles } from "@material-ui/core";
import React from "react";
import { useState } from "react";
import {
  Form,
  FormInput,
  FormLabel,
  FormOption,
  FormSelect,
} from "../ProjectsElements";

const useStyles = makeStyles((theme) => ({
  button: {
    marginTop: "3%",
    width: "20%",
    fontWeight: 700,
    border: "1px solid rgba(0,0,0,.8)",
    "&:nth-child(1)": {
      marginRight: "2%",
    },
  },
}));

const Termination = () => {
  const [finish, setFinish] = useState("Nie");
  const classes = useStyles();

  return (
    <Form>
      <FormLabel>
        Data zakoczenia udziau w projekcie:
        <FormInput type="date"></FormInput>
      </FormLabel>
      <FormLabel>
        Czy UP zakoczy zgodnie ze ciek:
        <FormSelect value={finish} onChange={(e) => setFinish(e.target.value)}>
          <FormOption>Nie</FormOption>
          <FormOption>Tak</FormOption>
          <FormOption>Nie dotyczny</FormOption>
        </FormSelect>
      </FormLabel>
      {finish === "Nie" ? (
        <FormLabel>
          Powd zakoczenia niezgodnie ze ciek:
          <FormSelect>
            <FormOption>Podjcie zatrudnienia</FormOption>
            <FormOption>Rezygnacja UP</FormOption>
            <FormOption>
              Skrelenie za nieobecnoci powyej 20% na szkoleniu
            </FormOption>
            <FormOption>
              Skrelenie za nieobecnoci powyej 20% na stau
            </FormOption>
            <FormOption>
              Skrelenie ze stau - 33 dni cigej nieobecnoci (L4)
            </FormOption>
            <FormOption>Nieusprawiedliwona </FormOption>
            <FormOption>
              Skrelenie ze stau - 33 dni cigej nieobecnoci (L4)
            </FormOption>
            <FormOption>
              Skrelenie ze stau - 33 dni cigej nieobecnoci (L4)
            </FormOption>
          </FormSelect>
        </FormLabel>
      ) : (
        <></>
      )}
      <FormLabel>
        Dokument potwierdzajcy zakoczenie udziau w projekcie:
        <FormInput></FormInput>
      </FormLabel>
      <FormLabel>
        Data dokumentu:
        <FormInput type="date"></FormInput>
      </FormLabel>
      <Button type="submit" variant="outlined" className={classes.button}>
        Zakocz udzia
      </Button>
    </Form>
  );
};

export default Termination;
